#fuel-calculator {
    width: 600px;  /* Aumentar más el ancho para encajar los campos más grandes */
    height: auto; 
    padding: 30px; /* Aumentar padding para dar más espacio */
    border: 1px solid #ccc;
    border-radius: 10px;
    font-family: Arial, sans-serif;
    box-sizing: border-box;
    position: relative;
    overflow: visible;
}
  
#fuel-calculator h2 {
    text-align: center;
    font-size: 14px; /* Aumentar tamaño de título */
}
  
form {
    display: flex;
    flex-direction: column;
    padding-bottom: 20px;
}
  
form div {
    margin-bottom: 15px; /* Aumentar espacio entre campos */
}
  
label {
    font-size: 14px; /* Aumentar tamaño de texto de las etiquetas */
    margin-bottom: 10px;
    display: inline-block;
}
  
input, select {
    width: 100%;
    padding: 15px; /* Aumentar padding para hacer los campos más grandes */
    font-size: 16px; /* Aumentar tamaño del texto dentro de los inputs */
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}
  
button {
    background-color: #4CAF50;
    color: white;
    padding: 15px 30px; /* Aumentar padding del botón */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px; /* Aumentar tamaño de texto del botón */
}
  
button:hover {
    background-color: #45a049;
}

.unit-info {
    margin-bottom: 20px;
    text-align: center;
}

.unit-section {
    display: flex;
    justify-content: space-between;
}

.unit-section div {
    width: 30%;
}


.input-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  
  .input-container div {
    width: 48%; /* Asegura que los campos tengan el mismo tamaño */
  }
  
  .input-container label {
    font-size: 14px;
    margin-bottom: 10px;
    display: inline-block;
  }
  
  .input-container input {
    width: 100%;
    padding: 15px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
  

  .price-gallons-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  
  .price-gallons-container div {
    width: 48%; /* Asegura que los campos tengan el mismo tamaño */
  }
  
  .price-gallons-container label {
    font-size: 13px;
    margin-bottom: 10px;
    display: inline-block;
  }
  
  .price-gallons-container input {
    width: 100%;
    padding: 15px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  /* Aseguramos que el símbolo Q esté dentro del campo de texto */
  input[name="pricePerGallon"] {
    padding-left: 30px; /* Espacio extra para el símbolo */
    background-image: url('https://upload.wikimedia.org/wikipedia/commons/5/5f/Guatemala_symbol.png'); /* Imagen del símbolo */
    background-repeat: no-repeat;
    background-position: 5px center; /* Posición del símbolo */
    background-size: 20px 20px; /* Ajuste de tamaño */
  }
  

  .rounded-result {
    display: block;  /* Mostrar solo en la interfaz */
  }
  
  /* Cuando exportas, se ocultará la sugerencia de redondeo */
  #fuel-calculator .rounded-result {
    display: none;
  }
  